import {Redirect, Link} from "react-router-dom";
import {useState} from "react";
import "../styles/login.css";

const Login = (props) => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("")
    const [error, setError] = useState("");
    const handleUsernameChange = (e) => {
        setUsername(e.target.value);
    }
    const handlePasswordChange = (e) => {
        setPassword(e.target.value);
    }
    const fields = Array.from(document.querySelectorAll(".field>input"));
    fields.forEach((item) => {
        item.style.border = "1px solid var(--code-background)";
    })
    const next = async (e) => {
        e.preventDefault();
        if (!e.isTrusted) {
            return;
        }
        let isOk = true;
        if (username === "") {
            isOk = false;
            document.getElementById("username").style.border = "1px solid red";
        }
        if (password === "") {
            isOk = false;
            document.getElementById("password").style.border = "1px solid red";
        }

        if (isOk === true) {
            const result = await props.fetchLogin(username, password);
            if (result.token) {
                props.setToken(result.token);
                return;
            }
            if (result.message === "User not found.") {
                document.getElementById("username").style.border = "1px solid red";
                setError("     ");
            } else if (result.message === "Password is incorrect.") {
                document.getElementById("password").style.border = "1px solid red";
                setError("  ");
            } else if (result.message === "Please verify your email.") {
                setError("     ");
            } else if (result.details && result.details.length) {
                console.log(result.details[0].context)
                if (result.details[0].context.label === "password") {
                    setError("        ");
                } else {
                    setError("       ")
                }
            } else {
                setError(result.message)
            }
        }
    }

    return (
        <div className="login">
            {props.user.id && <Redirect to="/accounts/dashboard" />}
            <form className="form" onSubmit={next}>
                <div className="header" style={{width: "100%"}}>
                    <h1></h1>
                </div>

                <div className="field">
                    <input id="username" name="username" type="text" placeholder="  "
                           onChange={handleUsernameChange}
                           value={username}/>
                    <input minLength="3" id="password" name="password" type="password" placeholder=""
                           onChange={handlePasswordChange}
                           value={password}/>
                </div>

                {error && <div className="error">{error}</div>}

                <div className="submit">
                    <input type="submit" value=""/>
                </div>
                <div className="spacer">
                    <div></div>
                    <h2></h2>
                    <div></div>
                </div>
                <div className="submit" style={{marginBottom: "15px"}}>
                    <Link to="/accounts/register">
                         
                    </Link>
                </div>
                <div className="submit">
                <Link to="/accounts/forgot-password" style={{width: "100%"}}>
                      
                </Link>
            </div>
            </form>
        </div>
    );
}

export default Login;